.timeline-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 40px 0;
}
.timeline-container::after {
    background-color: #170B64;
    content: '';
    position: absolute;
    left: calc(50% - 2px);
    width: 4px;
    height: 100%;
}
.timeline-item {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
    position: relative;
    width: 46%;
}
.timeline-item:nth-child(odd) {
    align-self: flex-end;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 0;
}
  
.timeline-item-content {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 15px;
    position: relative;
    width: 600px;
    text-align: right; 
}
.timeline-item-content::after {
    content: ' ';
    background-color: #fff;
    box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
    position: absolute;
    right: -7.5px;
    top: calc(50% - 7.5px);
    transform: rotate(45deg);
    width: 15px;
    height: 15px;
}
.timeline-item:nth-child(odd) .timeline-item-content {
    text-align: left;
    align-items: flex-start;
}
.timeline-item:nth-child(odd) .timeline-item-content::after {
    right: auto;
    left: -7.5px;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}
  
.timeline-item-content .tag {
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    top: 5px;
    left: 5px;
    letter-spacing: 1px;
    padding: 5px;
    position: absolute;
    text-transform: uppercase;
}
.timeline-item:nth-child(odd) .timeline-item-content .tag {
    left: auto;
    right: 5px;
}
.timeline-item-content time {
    color: #777;
    font-size: 13px;
    font-weight: bold;
}
.timeline-item-content p {
    color: black;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    margin: 15px 0;
    max-width: 350px;
}
.timeline-item-content a {
    font-size: 14px;
    font-weight: bold;
}
.timeline-item-content a::after {
    content: ' â–º';
    font-size: 12px;
}
.timeline-item-content .circle {
    background-color: #fff;
    border: 3px solid #170B64;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 10px);
    right: -40px;
    width: 20px;
    height: 20px;
    z-index: 100;
}
.timeline-item:nth-child(odd) .timeline-item-content .circle {
    right: auto;
    left: -40px;
}
@media only screen and (max-width: 720px) {
    .timeline-item-content {
        width: 500px;
    }
    .timeline-item{
        width: 45%;
    }
}
@media only screen and (max-width: 600px) {
    .timeline-item-content {
        width: 400px;
    }
    .timeline-item{
        width: 44%;
    }
    
}
@media only screen and (max-width: 480px) {
    .timeline-item-content {
        width: 300px;
    }
    .timeline-item{
        width: 42.5%;
    }
    .timeline-item-content .tag{
        display: none;
    }
    .timeline-item-content p{
        font-size: 14.5px;
    }
    .timeline-item-content time{
        font-size: 11px;
    }
}
@media only screen and (max-width: 380px) {
    .timeline-item-content {
        width: 200px;
    }
    .timeline-item{
        width: 39.5%;
    }
    .timeline-item-content .tag{
        display: none;
    }
    .timeline-item-content p{
        font-size: 13px;
    }
    .timeline-item-content time{
        font-size: 8.5px;
    }
}


